.container {
  width: 100vw;
  height: 100vh;
  position: fixed;
  background-color: rgba(39, 39, 42, 0.644);
  display: flex;
  align-items: center;
  justify-content: center;
}

.loader {
  width: 150px;
  height: 150px;
  background-image: url(../../images/loader.svg);
  background-position: center;
  background-repeat: no-repeat;
}

.hidden {
  display: none;
}
